@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Mono&display=swap');

header {
    background-color: #ececec;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: black;
    padding: 0 15px;
    width: 100%;
    font-family: 'IBM Plex Mono', monospace;
    font-weight: 500;
    font-size: 1.3rem;
}

header h1 {
    text-align: center;
}

header li {
    list-style: none;
}

header a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
    opacity: 0.9;
}

header a:hover {
    opacity: 1;
}

header nav {
    display: flex;
}

header nav li {
    margin: 2rem 15px;
}

header nav li:hover {
    opacity: 0.5;
}

header nav li:first-child {
    margin-left: 0;
}

header nav li:last-child {
    margin-right: 12rem;
}

@media screen and (max-width: 600px) {
    header {
        align-content: center;
        justify-content: flex-start;
        margin-left: 35px;
        font-size: 0.7rem;
    }
    
    header nav li {
        justify-content: space-evenly;
    }

    .nav-item {
        margin: 0;
    }
}

