body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ececec;
}


.App {
  /* text-align: center; */
  flex-direction: column;
  align-items: center;
  background: #ececec;
  font-family: 'Roboto', sans-serif;
  margin: auto 10px;
  width: auto;
  height: 2000px;
}

.app-container {
  margin: 0 7.5rem;
}

@media screen and (max-width: 600px) {
  .app-container {
    margin: 0 2.5rem;
  }
}


header {
    background-color: #ececec;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: black;
    padding: 0 15px;
    width: 100%;
    font-family: 'IBM Plex Mono', monospace;
    font-weight: 500;
    font-size: 1.3rem;
}

header h1 {
    text-align: center;
}

header li {
    list-style: none;
}

header a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
    opacity: 0.9;
}

header a:hover {
    opacity: 1;
}

header nav {
    display: flex;
}

header nav li {
    margin: 2rem 15px;
}

header nav li:hover {
    opacity: 0.5;
}

header nav li:first-child {
    margin-left: 0;
}

header nav li:last-child {
    margin-right: 12rem;
}

@media screen and (max-width: 600px) {
    header {
        align-content: center;
        justify-content: flex-start;
        margin-left: 35px;
        font-size: 0.7rem;
    }
    
    header nav li {
        justify-content: space-evenly;
    }

    .nav-item {
        margin: 0;
    }
}


.banner {
    display: flex;
    flex-direction: row;
    margin-top: 4rem;
    margin-bottom: 4rem;
    margin-left: 0;
    margin-right: 1rem;
}

.banner-pic {
    max-width: 25%;
    max-height: 25%;
    border-radius: 50%;
    margin-left: 4.5rem;
}

.typed-text {
    margin-top: 5rem;
    margin-left: 6rem;
    font-family: 'IBM Plex Mono', monospace;
    font-weight: 500;
}

.typewriter {
    display: flex;
    justify-content: flex-start;
    font-size: 48px;
    margin-bottom: 50px;
    letter-spacing: 5px;
}

.typewriter.first {
    overflow: hidden;
    border-right: .15em solid black;
    white-space: nowrap;
    -webkit-animation: 
        typing 3.5s steps(40, end),
        blink-caret .75s step-end infinite;
            animation: 
        typing 3.5s steps(40, end),
        blink-caret .75s step-end infinite;
}

@-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

@-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: black; }
}

@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: black; }
}

@media screen and (max-width: 600px) {
    
    .banner {
        display: flex;
        flex-direction: column;
        margin-top: 1.5rem;
        margin-bottom: 0rem;
        margin-left: 0;
        margin-right: 1rem;
    }
    
    
    .banner-pic {
        max-width: 180px;
        max-height: 180px;
        border-radius: 50%;
        
    }

    .typed-text {
        margin-top: 2rem;
        margin-left: 2.2rem;
        font-family: 'IBM Plex Mono', monospace;
        font-weight: 500;
    }

    .typewriter {
        display: flex;
        justify-content: flex-start;
        font-size: 17px;
        margin-bottom: 30px;
        letter-spacing: 5px;
    }
}
.social-media-icons {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.link-item {
    width: 5%;
    height: 5%;
    margin: 0 30px;
}

.link-item:hover {
    opacity: 0.6;
}

.link-item:first-child {
    margin-left: 80px;
}

@media screen and (max-width: 600px) {

    .social-media-icons {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
    }

    .link-item {
        width: 10%;
        height: 5%;
        margin: 0 15px;
    }

    .link-item:hover {
        opacity: 0.6;
    }

}
